import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/query/react";
import {API_URL} from "./productsService";
import {CartProduct} from "../models/CartProduct";

export const cartApi = createApi({
    reducerPath: 'cartApi',
    baseQuery: fetchBaseQuery({baseUrl: API_URL}),
    tagTypes: ['cartItem'],
    endpoints: (build) => ({
        getCartItems: build.query<CartProduct[], any>({
            query: () => ({
                url: '/cart'
            })
        }),
        setCartItem: build.mutation<CartProduct, CartProduct>({
            query: (cartItem) => ({
                url: '/cart',
                method: 'POST',
                body: cartItem,
                headers: {
                    'Content-type': 'application/json; charset=UTF-8',
                }
            }),
            async onQueryStarted(cartItem, {dispatch, queryFulfilled}) {
                const updateCart = dispatch(
                    cartApi.util.updateQueryData(
                        'getCartItems', '', (draft) => {
                            draft.push(cartItem)
                        }
                    )
                )
                queryFulfilled.catch(updateCart.undo)
            }
        }),
        removeCartItem: build.mutation<CartProduct, string>({
            query: (id) => ({
                url: `/cart/${id}`,
                method: 'DELETE'
            }),
            async onQueryStarted(id, {dispatch, queryFulfilled}) {
                const deleteCartItem = dispatch(
                    cartApi.util.updateQueryData('getCartItems', '', (draft) => {
                        let itemIndex = draft.findIndex( draftedItem => draftedItem.id !== id )
                        draft.splice( itemIndex, 1 )
                    })
                )
                queryFulfilled.catch(deleteCartItem.undo)
            }
        }),
        modifyCount: build.mutation<CartProduct, CartProduct>({
            query: (cartProduct) => ({
                url: `/cart/${cartProduct.id}`,
                method: 'PUT',
                body: cartProduct,
                headers: {
                    'Content-type': 'application/json; charset=UTF-8',
                }
            }),
            async onQueryStarted({id, count: updatedCount}, {dispatch, queryFulfilled}) {
                const updateCount = dispatch(
                    cartApi.util.updateQueryData(
                        'getCartItems', '', (draft) => {
                            draft.forEach( item => {
                                if (item.id === id) item.count = updatedCount
                            })
                        }
                    )
                )
                queryFulfilled.catch(updateCount.undo)
            }
        })
    })
})

export const {useGetCartItemsQuery, useSetCartItemMutation, useModifyCountMutation, useRemoveCartItemMutation} = cartApi