import React, {FC} from 'react';
import IndeterminateCheckBoxIcon from "@mui/icons-material/IndeterminateCheckBox";
import {AddBox} from "@mui/icons-material";
import {CartProduct} from "../../models/CartProduct";
import {useModifyCountMutation, useRemoveCartItemMutation} from "../../services/cartService";
import {Alert} from "@mui/material";

const CountModifier: FC<CartProduct> = (cartItem) => {
    const [
        modifyCount, {isError: countHasError, isLoading: countIsLoading}
    ] = useModifyCountMutation()
    const [
        removeCartItem, {isError: removeError, isLoading: removeIsLoading}
    ] = useRemoveCartItemMutation()

    let [showAlert, setShowAlert] = React.useState(true)
    let itemCount = cartItem.count
    let editCount = (action: 'increase' | 'decrease') => {
        if (!countHasError && !countIsLoading) action === 'increase'
            ? modifyCount({...cartItem, count: ++itemCount})
            : modifyCount({...cartItem, count: --itemCount})
    }

    const handleIncrease = () => editCount('increase')
    const handleDecrease = () => editCount('decrease')
    const handleAlertClose = () => setShowAlert(false)
    const handleItemRemove = () => !removeError && !removeIsLoading && removeCartItem(cartItem.id)

    let btnOpacity = countIsLoading ? 0.3 : 1
    return (<>
        <span className="flex">
            <span className="cart-features">
                <IndeterminateCheckBoxIcon
                    fontSize='small'
                    fillOpacity={btnOpacity}
                    onClick={handleDecrease}
                />
                <span className="cart-count">{cartItem.count}</span>
                <AddBox
                    onClick={handleIncrease}
                    fontSize='small'
                    fillOpacity={btnOpacity}
                />
            </span>
            <span className='link-style' onClick={handleItemRemove}>remove</span>
        </span>
        {countHasError && showAlert &&
            <Alert
                className='alert'
                onClose={handleAlertClose}
                severity="warning"
            >
                Too many requests!
            </Alert>
        }
    </>);
};

export default CountModifier;