import React from 'react';
import {Modal, Paper} from "@mui/material";
import HeaderWithClose from "../common/HeaderWithClose";
import {useTypedDispatch, useTypedSelector} from "../../hooks/redux";
import {setCartItemsCount, setIsCartModalOpened} from "../../store/slices/cartSlice";
import {useGetCartItemsQuery} from "../../services/cartService";
import LoadingError from "../LoadingError";
import CartSingleItem from "./CartSingleItem";
import CartIsEmpty from "./CartIsEmpty";

const Cart = () => {
    const {isCartModalOpened} = useTypedSelector(state => state.cartSlice)
    const {data: cartItems, isError, isLoading, isFetching} = useGetCartItemsQuery('')
    const dispatch = useTypedDispatch()
    const handleClose = () => dispatch(setIsCartModalOpened(false))
    let cartItemsCount = React.useMemo(
        () => cartItems ? cartItems.length : 0,
        [cartItems]
    )
    React.useEffect(
        () => {
            if (cartItemsCount > 0) dispatch(setCartItemsCount(cartItemsCount))
        },
        [cartItems]
    )
    let title = `${cartItemsCount} items in your cart`
    return (
        <Modal
            open={isCartModalOpened}
            onClose={handleClose}
            keepMounted
        >
            <Paper className='modal-window'>
                <HeaderWithClose
                    title={title}
                    handleClose={setIsCartModalOpened}
                />
                {!isFetching && cartItems && cartItems.map( item =>
                    <CartSingleItem key={item.id} cartItem={item} />
                )}
                {isError && <LoadingError />}
                {cartItems && cartItems.length < 1 &&
                    <CartIsEmpty />
                }
            </Paper>
        </Modal>
    );
};

export default Cart;