import React, {FC} from 'react';
import {Card, CardContent, CardMedia, Typography} from "@mui/material";
import {NavLink} from "react-router-dom";
import {IProductCard} from "../../models/IProductCard";
import ProductCardHeader from "./ProductCardHeader";
import ProductSizes from "./ProductSizes";
import {useGetCartItemsQuery, useModifyCountMutation, useSetCartItemMutation} from "../../services/cartService";
import {useTypedDispatch, useTypedSelector} from "../../hooks/redux";
import {setIsCartModalOpened} from "../../store/slices/cartSlice";

const ProductCard: FC<IProductCard> = ({
    name, price, image, isNew, isFavorite, sizes, id, color, createdAt
}) => {
    let cartItemsCount = useTypedSelector(state => state.cartSlice.cartItemsCount)
    const [setCartItem] = useSetCartItemMutation()
    const [modifyCount] = useModifyCountMutation()
    const {data: cartItems} = useGetCartItemsQuery('')
    let [selectedSize, setSelectedSize] = React.useState('')
    const dispatch = useTypedDispatch()
    const addToCard = (e: React.MouseEvent<HTMLButtonElement>) => {
        e.preventDefault()
        const itemInCart = cartItems && cartItems.find( item => item.name === name && item.size === selectedSize )
        if (itemInCart) {
            let itemCount = itemInCart.count
            modifyCount({...itemInCart, count: ++itemCount}).unwrap()
        } else {
            let idInPayload = cartItemsCount > 0 ? String(++cartItemsCount) : '1'
            setCartItem({
                name, price, image, size: selectedSize, createdAt, id: idInPayload, color, count: 1
            }).unwrap()
        }
        dispatch(setIsCartModalOpened(true))
    }
    return (
        <Card className='product-card'>
            <ProductCardHeader isNew={isNew} isFavorite={isFavorite} />
            <NavLink to={`/productName`} >
                <div className="image-wrapper">
                    <CardMedia
                        component='img'
                        image={image}
                        alt='product card'
                    />
                    <ProductSizes
                        sizes={sizes}
                        setSelectedSize={setSelectedSize}
                        selectedSize={selectedSize}
                        addToCart={addToCard}
                    />
                </div>
                <CardContent sx={{px: 0}}>
                    <Typography
                        component='div'
                        gutterBottom
                        variant='h3'
                    >
                        {name}
                    </Typography>
                    <Typography
                        component='div'
                        variant='caption'
                    >
                        ${price}
                    </Typography>
                </CardContent>
            </NavLink>
        </Card>
    );
};

export default ProductCard;
