import {createSlice} from "@reduxjs/toolkit";

interface InitialState {
    isCartModalOpened: boolean
    cartItemsCount: number
}

const initialState: InitialState = {
    isCartModalOpened: false,
    cartItemsCount: 0
}

const cartSlice = createSlice({
    name: 'cartSlice',
    initialState,
    reducers: {
        setIsCartModalOpened(state, action) {
            state.isCartModalOpened = action.payload
        },
        setCartItemsCount(state, action) {
            state.cartItemsCount = action.payload
        }
    }
})

export const {setIsCartModalOpened, setCartItemsCount} = cartSlice.actions
export default cartSlice.reducer